<template>
  <div style="margin: 20px">
    <Form ref="formValidate" :model="articletype" :rules="ruleValidate" label-position="left" :label-width="120">
      <FormItem label="分类名称" prop="name">
        <Input v-model="articletype.name" placeholder="请输入分类名称！" size="large"></Input>
      </FormItem>
      <FormItem label="排序数值" prop='sortvalue'>
        <Row>
          <Col span='4'>
          <InputNumber v-model='articletype.sortvalue' :min='0'></InputNumber>
          </Col>
          <Col span='10'>
          <span style="color:silver">数值越大，在父级分类中的顺序越靠前</span>
          </Col>
        </Row>
      </FormItem>
      <FormItem>
        <Button type="primary" :loading="loading" @click="handleSubmit('formValidate')" size="large" style="width:120px;">
          <span v-if="!loading">提交</span>
          <span v-else>正在保存..</span>
        </Button>
        <Button type="ghost" @click="handleReset('formValidate')" size="large" style="margin-left: 8px;width:80px">取消</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      articetypeid: this.$route.query.id,
      loading: false,
      articletype: {
        name: '',
        sortvalue: 0,
      },
      ruleValidate: {
        name: [
          { required: true, message: '分类名称不能为空！', trigger: 'blur' }
        ],
        sortvalue: [
          { type:'number',required: true, message: '请输入排序数值！', trigger: 'blur' }
        ]
      }
    }
  },
  mounted: function() {
    this.$sitemap.prvname = "内容分类";
    this.$sitemap.currname = '编辑分类';
    this.getArticleType();

  },
  methods: {
    handleSubmit(name) {
      var thatform = this.$refs[name];
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.editArticleType(thatform);
        } else {
          this.$Message.error('还没有完整的信息输入！');
        }
      })
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    editArticleType: function(thatform) {
      var that = this;
      this.$http.post("articletype/edit", that.articletype).then(function(res) {
        that.loading = false;
        if (res.data.result == 1) {
          thatform.resetFields();
          that.$Message.success('内容分类修改成功!');
        } else
          that.$Message.error('修改失败，请检查原因！');
      });
    },
    getArticleType: function() {
      var that = this;
      var params = { typeid: parseInt(this.articetypeid) }
      this.$http.post("articletype/get", params).then(function(res) {
        that.articletype = res.data.data;
        that.loading = false;
        that.$Loading.finish();
      }).catch(function(res) {
        that.loading = false;
        that.$Loading.error();
      })
    }
  }
}

</script>



